import React from 'react';
import CountUp from 'react-countup';
import { useInView } from 'react-intersection-observer';
import './css/StatsSection.css';

const StatsSection = () => {
  const stats = [
    { count: 900, label: 'Listings' },
    { count: 400, label: 'Listing Categories' },
    { count: 6500, label: 'Visitors' },
    { count: 5000, label: 'Happy Clients' },
  ];

  const { ref, inView } = useInView({
    triggerOnce: true,  // ensures the animation only happens once
    threshold: 0.3,     // percentage of the section visible before triggering
  });

  return (
    <div className="stats-section" ref={ref}>
      <div className="stats-container">
        {stats.map((stat, index) => (
          <div key={index} className="stat-item">
            {inView && (
              <h2 className="stat-count">
                <CountUp start={0} end={stat.count} duration={3} separator="," />
                K+
              </h2>
            )}
            <p className="stat-label">{stat.label}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default StatsSection;
